@font-face {
    font-family: Minecraftia;
    src: url(data:font/truetype;base64,);
  }
  
  
   @import url("https://fonts.googleapis.com/css?family=Barlow|Tomorrow:400,700&display=swap");

body{
    background-color: #001D31;
}

*{
    list-style: none;
    box-sizing: border-box;
    padding: 0;
    text-decoration: none;
 }


.app{
    min-height: 100vh;
    scroll-snap-type: y mandatory;
}

.main_content{
    margin-left: 110px;
}

section{
    align-items: center;
    justify-content: center;
    
}

.red-title{
    font-family: Tomorrow, sans-serif;
    color: #F500BD;
    text-align: center;
    font-size: 200%;
}

.green-title{
    font-family: Tomorrow, sans-serif;
    color:#00BE67;
    text-align: center;
    font-size: 200%;
}


@media screen and (max-width: 760px) {
    .app{
        justify-content: center;
        align-items: center;
    }
    .main_content{
        justify-content: center;
        margin-right: 6%;
        margin-left: 6%;
    }
}

@media screen and (max-width:280px){
    .main_content{
        font-size: 10px;
    }
}